.home {
    .info-section {
        padding: 54px;
        width: 35%;
    }

    .content-area {
        background: $color3;
        margin-left: 35%;;
        width: 65%;
        background: white;
        height: 100%;
        // padding: 0 0 0 25%;
        .img-container {
            display: inline-block;
            width: 30%;
            position: relative;
            height: 100%;

            .user-img{
                position: absolute;
                top: 56px;
                left: -250px;
                width: 500px;
                height: calc(100% - 112px);
                background: url(../img/main-img.jpg) no-repeat center center; 
                -webkit-background-size: cover;
                -moz-background-size: cover;
                -o-background-size: cover;
                background-size: cover;
                -webkit-box-shadow: 0px 7px 20px 5px rgba(118,122,128,0.25);
                -moz-box-shadow: 0px 7px 20px 5px rgba(118,122,128,0.25);
                box-shadow: 0px 7px 20px 5px rgba(118,122,128,0.25);
            }
        }
        
        .profile-section {
            vertical-align: top;
            display: inline-block;;
            padding: 30% 0 0 0;
            max-width: 500px;

            .profile {
                h1 {
                    font-size: 2.3rem;
                    font-weight: 500;
                    line-height: 1.2;
                    margin: 0 0 1rem;
                    span {
                        color: $color1;
                    }
                }
            }

            h5 {
                font-size: 1.1rem;
                margin: 0 0 2rem;
            }

            p {
                color: $color5;
                font-weight: 300;
                line-height: 1.4;
                margin: 0 0 2rem;
            }
        }
    }
}

//phone
@media (max-width: 767px) {
    .home {
        .menu-icon {
            top: -3px;
            color: white;
            right: 20px;
            .fa {
                color: white;

            }
        }

        .content-area {
            height: auto;
            &:after {
                content: "";
                display: table;
                clear: both;
            }
        }
        .info-section {
            width: 30%;
            padding: 56px 30px;

            .icon-section {
                a {
                    font-size: 1.4rem;
                }
            }    

            .logo {
                height: 50px;
                width: 70px;
                font-size: 1rem;
            }
            .info-box {
                display: none;
            }
        }

        .content-area{
            margin-left: 0;
            width: 100%;

            &:after {
                content: "";
                display: table;
                clear: both;
            }

            .img-container {
                padding: 56px 20px 0;
                .user-img {
                    position: static;
                    width: 100%;
                }
            }
            .profile-section {
                width: 70%;
                float: right;
                padding: 20% 20px 0;


            }
        }
    }   
    
}
//tablet
@media (min-width: 768px) and (max-width: 991px) {
    .home {
        .info-section {
            width: 30%;
            padding: 56px 30px;

            .icon-section {
                a {
                    font-size: 1.4rem;
                }
            }
        }
        
        .content-area{
            margin-left: 30%;
            width: 70%;
            .img-container {
                padding: 56px 20px 0;
                .user-img {
                    position: static;
                    width: 100%;
                }
            }
            .profile-section {
                width: 70%;
                float: right;
                padding: 20% 20px 0;
            }
        }
    }   
}

// laptop
@media (min-width: 992px) and (max-width: 1199px) { 
    .home {
        .content-area{
            .img-container {
                padding: 56px 20px 0;
                float: left;
                .user-img {
                    position: static;
                    width: 100%;
                }
            }
            .profile-section {
                width: 70%;
                float: left;
                padding: 20% 20px 0;
                max-width: 800px;
            }

        }
    }   
}

//extra big
@media (min-width: 1200px) { 
    .home {
        .info-section {
            width: 20%;
            padding: 56px 30px;

            .icon-section {
                a {
                    font-size: 1.4rem;
                }
            }
        }
        .content-area{
            width: 80%;
            margin-left: 20%;

            .img-container {
                float: left;
                padding: 56px 20px 0;

                .user-img {
                    position: static;
                    width: 100%;
                }
            }
            .profile-section {
                width: 70%;
                float: left;
                padding: 20% 60px 0;
                max-width: 800px;
                // padding: 20% 20px 0;
            }
        }
    }   
}